<template>
    
    <div class="container-fluid py-4">
        <h1 class="text-center mb-4 title-form ">Formulario Aprendiz</h1>
        <Form class="container-fluid form-aprendiz" id="form-apre" @Submit.prevent="guardar">
            <div class="row g-3">
        <div class="col-md-6">
            <div class="form-floating">
            <input class="form-control" type="text" name="codigo" id="codigo" required placeholder="Codigo" v-model="codigo">
            <label class="text-success"  for="codigo"> Codigo</label>
        </div>
    </div>
        <div class="col-md-6">
            <div class="form-floating">
            <input class="form-control" type="text" name="nombre" id="nombre" placeholder="Nombre" required v-model="nombre">
            <label class="text-success" for="nombre"> Nombre</label>
            
        </div>
    </div>
        <div class="col-md-6">
            <div class="form-floating">
            <input class="form-control" type="text" name="apellido" id="apellido" placeholder="Apellido" required v-model="apellido">
            <label class="text-success" for="apedillo"> Apellido</label>

</div>
        </div>
        <div class="col-md-6">
            <div class="form-floating">
            <input class="form-control" type="number" name="edad" id="edad" placeholder="Edad" required v-model="edad">
            <label class="text-success" for="edad"> Edad</label>
            
        </div></div>
        <div class="col-md-6">
            <div class="form-floating">
            <input class="form-control" type="text" name="fecha-nac" id="fecha-nac" placeholder="Fecha de Nacimiento" required v-model="fechanac">
            <label class="text-success" for="fechanac"> Fecha Nacimiento</label>
            
        </div></div>
        <div class="col-md-6">
            <div class="form-floating">
            <input class="form-control" type="text" name="dir" id="dir" placeholder="Dirrecion" required v-model="direccion">
            <label class="text-success" for="dir"> Direccion</label>
            
        </div></div>
        <div class="col-md-6">
            <div class="form-floating">
            <input class="form-control" type="text" name="tel" id="tel" placeholder="Telefono" required v-model="tel"> 
            <label class="text-success" for="tel"> Telefono</label>
            
        </div> </div>
        <br>
    </div>
    </Form>
    <div class="d-grid gap-2 d-md-flex justify-content-md-end mt-4">
        <button class="btn btn-outline-success btn-action" id="consultar" name="consultar" @click="consultar" type="button">Consultar <i class="bi bi-search"></i></button> <br>
        <button class="btn btn-outline-success btn-action" id="guardar" name="guardar" type="submit" @click="guardar">Guardar <i class="bi bi-floppy"></i></button> <br>
        <button class="btn btn-outline-success btn-action" id="actualizar" name="actualizar" @click="actualizar" type="button">Actualizar <i class="bi bi-arrow-repeat"></i></button> <br>
        <button class="btn btn-outline-danger btn-action" id="eliminar" name="eliminar" @click="eliminar" type="button">Eliminar <i class="bi bi-trash"></i></button> <br>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default{

    data(){
    

return{
    codigo:"",
    nombre:"",
    apellido:"",
    edad:"",
    fechanac:"",
    direccion:"",
    tel:""
};
},
methods:{
    guardar(){
        axios
        .post("http://localhost:8081/api/aprendiz",
            {
                codigo:this.codigo,
                nombre:this.nombre,
                apellido:this.apellido,
                edad:this.edad,
                fechanac:this.fechanac,
                direccion:this.direccion,
                tel:this.tel,
            }
        )
        .then((response)=>{
            console.log("Aprendiz registrado con exito",response.data);
            alert("registro exitoso");
                this.codigo="";
                this.nombre="";
                this.apellido="";
                this.edad="";
                this.fechanac="";
                this.direccion="";
                this.tel="";
                this.$emit("actualizar-tabla")
        })
        .catch((error)=>{
            console.error("error al registar al aprendiz",error)
            alert("Error al registrar");
        });
        
    },
    consultar(){ 
        axios
        .get("http://localhost:8081/api/aprendiz/"+this.codigo)
        .then((response)=>{
            console.log(response.data);
            this.nombre=response.data.nombre;
            this.apellido=response.data.apellido;
            this.edad=response.data.edad;
            this.fechanac=response.data.fechanac;
            this.direccion=response.data.direccion;
            this.tel=response.data.tel;
            this.$emit("actualizar-tabla")
        })
        .catch((error)=>{
            alert("error al consultar",error);
            console.log("Error al consultar")
        })
    },
    actualizar(){
        axios
        .put("http://localhost:8081/api/aprendiz/actualizar/"+this.codigo,{

        codigo:this.codigo,
        nombre:this.nombre,
        apellido:this.apellido,
        edad:this.edad,
        fechanac:this.fechanac,
        direccion:this.direccion,
        tel:this.tel
        
        
        })
        .then((response)=>{
            alert("Aprendiz actualizado con exito")
            console.log("Aprendiz actualizado correctamente",response.data);
            this.$emit("actualizar-tabla")
        })
        .catch((error)=>{
            console.error("error al actualizar el aprendiz", error);
        })
    },
    eliminar(){
        axios
        .delete("http://localhost:8081/api/aprendiz/"+this.codigo)
        .then(()=>{
            alert("aprendiz Eliminado")
            console.log("Aprendiz eliminado");
            this.codigo="";
            this.nombre="";
            this.apellido="";
            this.edad="";
            this.fechanac="";
            this.direccion="";
            this.tel="";
            this.$emit("actualizar-tabla")
        })
        .catch(()=>{
            console.error("Fallo al eliminar aprendiz");
            alert("error al eliminar aprendiz")
        })
    }
},

};

</script>

<style scoped>
.title-form {
    color: #38A900;
    font-weight: 700;
    text-shadow: 1px 1px 2px rgba(0,0,0,0.1);
    border-bottom: 3px solid #38A900;
    display: inline-block;
    padding-bottom: 0.5rem;
}

.form-aprendiz {
    background: rgb(255, 255, 255);
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.1);
}

.form-floating label {
    font-weight: 500;
    opacity: 0.8;
}

.form-control:focus {
    border-color: #38A900;
    box-shadow: 0 0 0 0.25rem rgba(56, 169, 0, 0.25);
}

.btn-action {
    min-width: 120px;
    transition: all 0.3s ease;
    border-radius: 0.5rem;
    padding: 0.5rem 1.5rem;
}

.btn-outline-success {
    border: 1px solid #38A900;
    color: #38A900;

}

.btn-success:hover {
    background: rgba(56, 169, 0, 0.1);
}

.btn-outline-success:hover {
    
    background: #2d8900;
    color: #fff;
}

</style>