<template>
    <div class="container-fluid py-4">
        <h1 class="title-table mb-4">TABLA APRENDIZ</h1>
        <div class="table-responsive rounded-3 shadow-sm">
        <table class="table table-hover align-middle">
            <thead class="table-success">
                <tr>
                    <th scope="col" class="ps-4">Codigo</th>
                    <th scope="col">Nombre</th>
                    <th scope="col">Apellido</th>
                    <th scope="col">Edad</th>
                    <th scope="col">Fecha Nacimiento</th>
                    <th scope="col">Direccion</th>
                    <th scope="col" class="pe-4">Telefono</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="aprendiz in aprendices" :key="aprendiz.codigo" class="table-row">
                    <td class="ps-4 fw-medium">{{aprendiz.codigo}}</td>
                    <td>{{aprendiz.nombre}}</td>
                    <td>{{aprendiz.apellido}}</td>
                    <td><span class="badge bg-success bg-opacity-10 text-success">
                        {{ aprendiz.edad }} años</span> </td>
                    <td>{{aprendiz.fechanac}}</td>
                    <td>{{aprendiz.direccion}}</td>
                    <td class="pe-4">
                            <i class="bi bi-telephone me-2"></i>{{ aprendiz.tel }}
                        </td>
                </tr>
            </tbody>
        </table>

    </div>
</div>
</template>

<script>
import axios from 'axios';
export default{
    data(){
        return{
            aprendices:[],
        };
    },
    methods:{
        obtenerAprendiz(){
            axios
            .get("http://localhost:8081/api/aprendiz/listar")
            .then((response)=>{
                this.aprendices=response.data;

            })
            .catch((error)=>{
                console.error("error al listar a los aprendices",error);

            });
        },

    },
    mounted(){
        this.obtenerAprendiz();
    },
};

</script>

<style scoped>
.title-table {
    color: #38A900;
    font-size: 1.8rem;
    font-weight: 700;
    letter-spacing: -0.5px;
}

.table-success {
    --bs-table-bg: #38A900;
    --bs-table-color: white;
    font-weight: 500;
}

.table-hover tbody tr:hover {
    background-color: rgba(56, 169, 0, 0.03);
    transform: translateX(4px);
    transition: all 0.2s ease;
}

.table-row {
    border-bottom: 1px solid rgba(0,0,0,0.05);
}

.badge {
    padding: 0.35em 0.65em;
    font-size: 0.85em;
    border-radius: 0.5rem;
}

.bi-telephone {
    color: #38A900;
    font-size: 1.1rem;
}
</style>